.project-images{
    height: 350px;
    width: 450px;
}

.project-body{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    grid-gap: 1rem;
    grid-auto-flow: row;
    width: 100%;
  justify-content: center;
}

.project-name{
    color: hsl(3, 100%, 89%);
}

.project-body a{
  color: hsl(319, 57%, 53%);
}

.item{
  text-align: center;
}

.project-list {
  list-style-type: none;
  color: hsl(3, 70%, 69%);
}

/* Media Query */
   @media only screen and (min-width: 600px){
    .project-body{
      grid-template-columns: repeat(2,1fr);
    }
  }
